.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

html,
body,
#root {
  height: 100%;
}

body {
  font-family: 'Poppins Regular';
  background-color: #eee;
  background-attachment: fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  min-height:100%;
  position:relative;
}

.center-content {
  max-width: 1200px;
  margin: 0 auto;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 60px;
}

main {
  padding: 20px 0 80px 0;
}

header, footer {
  padding: 0 20px;
  height: 60px;
  background-color: #4085B1;
}

header {
  display: flex;
  justify-content: space-between;
}

header nav, header nav ul, header nav ul li {
  height: 100%;
}
header nav ul {
  display: flex;
}

header nav ul li {
  margin: 0;
  padding: 0;
  border-radius: 0;
}

header nav ul li .link {
  display: block;
  padding: 20px;
  width: 100%;
  height: 100%;
}

header nav ul li:hover {
  background-color: rgb(60, 124, 163);
}

header nav ul li .active {
  background-color: rgb(72, 149, 197);
}

.header_link {
  display: flex;
  justify-content: space-between;
  font-size: 42px;
  font-weight: bold;
  font-style: italic;
}

footer {
  position: absolute;
  display: flex;
  bottom: 0;
  padding: 20px;
  width: 100%;
  color: #fff;
  justify-content: flex-end;
  font-weight: bold;
}

ul {
  width: 100%;
  padding-left: 10px
}

ul li {
  padding: 5px 0 5px 10px;
  margin: 5px 0;
  cursor: pointer;
  border-radius: 5px;
}

ul li:hover {
  background-color: #4085B1;
  color: #fff;
}

.link {
  color: #fff;
}

.colored-link {
  color: #4085B1;
}

.dark-link {
  color: #000;
}

ul li:hover .link, ul li:hover .dark-link, ul li:hover .colored-link {
  color: #fff;
}

.bold {
  font-weight: bold;
}

h3 {
  font-size: 24px;
}

.shadow {
  box-shadow: 0px 1px 5px rgba(0,0,0,0.5);
  border-collapse: collapse;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navigation {
    background-color: #fff;
    -webkit-transition: all 1s;
    transition: all 1s;
    overflow: hidden;
    margin-bottom: 5px;
}

.loading {
    min-height: 110px;
}

.navigation.collapsed-false {
    max-height: 70px;
}

.navigation.collapsed-true {
    max-height: 220px;
}

.navigation.collapsed-false i {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    -webkit-transition: all 1s;
    transition: all 1s;
}

.navigation.collapsed-true i {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    -webkit-transition: all 1s;
    transition: all 1s;
}

.navigation_main-content, .navigation_short-info {
    display: flex;
}

.navigation-active-filter {
    font-size: 28px;
    color: #4085B1;
    font-weight: bold;
}

.navigation_short-info {
    justify-content: space-between;
    padding: 20px 60px 20px 20px;
    position: relative;
    border-bottom: 5px solid #eee;
}

.navigation_short-info_collapse_btn {
    position: absolute;
    top: 17px;
    right: 15px;
    cursor: pointer;
    padding: 5px;
    font-size: 24px;
    border-radius: 5px;
    color: #4085B1;
}

.navigation_short-info_collapse_btn:hover {
    background-color: #eee;
    color: #000;
}

.navigation_short-info form {
    display: flex;
}

.navigation_short-info form input[type="text"] {
    border: 1px solid #4085B1;
    padding: 5px;
}

.navigation_search_btn {
    border: none;
    padding: 5px;
    cursor: pointer;
    border-left: none;
    background-color: #4085B1;
    border: 1px solid #4085B1;
    border-left: none;
    color: #fff;
}

.navigation_search_btn:hover {
    color: #4085B1;
    background-color: #fff;
}

.navigation_main-content article {
    padding-right: 10px;
}

.navigation_main-content article:not(:first-child) {
    border-left: 5px solid #eee;
}

.navigation-genres-list {
    min-width: 60%;
    max-width: 60%;
}

.navigation-genres-list ul, .navigation-filters-list ul{
    display: flex;
    flex-wrap: wrap;
}

.navigation-genres-list ul li {
    width: 20%;
}

.navigation-filters-list ul li {
    width: 100%;
}

.navigation-filters-list {
    min-width: 13%;
    max-width: 13%;
}

.navigation-search-container, .navigation-search-results {
    min-width: 27%;
    max-width: 27%;
    padding: 5px 10px;
}

.navigation-search-results figure {
    display: flex;
    align-items: center;
}

.navigation-search-results figure img {
    max-width: 20px;
    max-height: 30px;
}

.navigation-search-results figure figcaption {
    padding-left: 20px;
    font-size: 12px;
    max-width: 80%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.loader-wrapper {
    text-align: center;
}

.loader-wrapper img {
    max-width: 100px;
    margin-bottom: 20px;
    margin: 0 auto;
    display: block;
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
}
.movie {
    margin-bottom: 5px;
    min-width: 24.7%;
    max-width: 24.7%;
    background-color: #fff;
    position: relative;
}

.movie_title {
    padding: 10px 60px 10px 20px;
    cursor: pointer;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 90%;
}

.movie_title:hover {
    background: -webkit-linear-gradient(left, #4085B1, #fff) ;
    background: linear-gradient(90deg, #4085B1, #fff) ;
    color: #fff;
}

.movie .movie-actions {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
}

.movie .movie-actions i {
    margin: 10px;
    cursor: pointer;
    color: #ccc;
}

.movie .movie-actions i.favorite.true, .movie .movie-actions i.favorite:hover {
    color: orangered;
}

.movie .movie-actions i.watchlist.true, .movie .movie-actions i.watchlist:hover {
    color: #4085B1;
}

.movie figure {
    display: flex;
}

.movie figure figcaption {
    width: 100%;
}

.movie_img-wrapper {
    min-width: 150px;
    min-height: 225px;
    max-width: 150px;
    max-height: 225px;
    margin-left: 20px;
}

.movie_img {
    width: 100%;
}

.movie-body {
    padding-top: 10px;
}

.movie-body_release {
    padding-left: 10px;
}

.movie-body_release span {
    display: block;
    margin: 10px 0 0 10px;
    font-weight: normal;
}

.movie-body_overview {
    padding: 20px;
    text-align: justify;
}

.movie-body_genres {
    padding: 0 10px 10px 10px;
}

.movie-body_genres ul {
    padding-left: 0;
}

.movie-body_vote {
    position: absolute;
    bottom: 10px;
    right: 10px;
    width: 100px;
    height: 20px;
    text-align: right;
}

.movie-body_vote img {
    width: 100%;
    position: absolute;
    z-index: 2;
    left: 0;
    top: 0;
}

/*zoom movie poster */
img[tabindex="0"] {
    cursor: -webkit-zoom-in;
    cursor: zoom-in;
}

img[tabindex="0"]:focus {
    position: fixed;
    z-index: 3;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: auto;
    height: auto;
    box-shadow: 0px 0px 10000px 10000px rgba(0, 0, 0, 0.7)
}

img[tabindex="0"]:focus {
    pointer-events: none;
}

.paginator {
    background-color: #fff;
    width: 100%;
    padding: 20px;
}

.paginator ul {
    display: flex;
    margin: 0;
    padding: 0;
    justify-content: center;
}

.paginator ul li {
    margin: 0 10px;
    padding: 10px;
    border-radius: 0;
    border: 1px solid #ccc;
    min-width: 35px;
    text-align: center;
}

.paginator ul li.active {
    background-color: #4085B1;
    color: #fff;
}

.paginator ul li.dotted_li.none {
    display: none;
}

.paginator ul li.dotted_li {
    padding: 10px 0 0 0;
    margin: 0;
    border: none;
}

.paginator ul li.dotted_li:hover {
    cursor: default;
    background-color: #fff;
    color: #222;
}
.movies-list {
    padding: 0;
}

.movies-list ul.main-content_movies-list {
    padding-left: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.movies-list .collection-information {
    background-color: #fff;
    margin-bottom: 5px;
}

.movies-list .collection-information img {
    width: 100%;
}

.movies-list .collection-information p {
    padding: 20px;
}

.movie-details {
    padding: 20px;
    background-color: #fff;
    position: relative;
    color: #444;
}

.movie-details article {
    border-top: 5px solid #eee;
    position: relative;
    padding: 20px;
}

.movie-details-article_title {
    position: absolute;
    top: 0;
    left: 20px;
    -webkit-transform: translate(0, -60%);
            transform: translate(0, -60%);
    background-color: #eee;
    width: 160px;
    padding: 5px 10px;
    border-radius: 5px;
    font-weight: bold;
}

.movie-details ul {
    display: flex;
}

.movie-details ul li {
    padding: 5px;
    max-width: 14%;
    min-width: 14%;
}

.movie-details ul li:hover .dark-link, .movie-details ul li:hover .colored-link {
    color: #fff;
}

.movie-details ul li figure img {
    display: block;
    max-width: 100%;
    margin: 0 auto;
    border: 1px solid #ccc;
}

.movie-details figure figcaption {
    margin-top: 10px;
    text-align: center;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

.movie-details_votes {
    display: block;
    position: absolute;
    top: 10px;
    right: 20px;
    width: 200px;
    font-size: 0;
    height: 40px;
}

.movie-details_title_status {
    display: inline-block;
    color: #fff;
    font-size: 16px;
    padding: 2px 5px;
    border-radius: 5px;
    vertical-align: top;
    letter-spacing: 1px;
    background-color: yellowgreen;
}

.status-Released {
    background-color: green;
}

.status-Planned {
    background-color: #4085B1;
}

.status-Canceled {
    background-color: #FF0000;
}

.status-Rumored {
    background-color: #ccc;
}

.movie-details_poster {
    width: 100%;
    margin-top: 20px;
}

.movie-details_tagline {
    text-align: right;
    font-style: italic;
}

.movie-details_tagline::before, .movie-details_tagline::after {
    content: '\'';
}

.movie-details_overview {
    margin: 10px 0 20px 0;
    padding: 10px 0;
    text-align: justify;
}

.movie-details_video .video-wrapper {
    width: 100%;
    display: flex;
}

.movie-details_video .video-wrapper .iframe-wrapper {
    width: 50%;
    background-color: #4085B1;
    padding: 20px;
}
.movie-details_video iframe {
    width: 100%;
    height: 300px;
}

.movie-details_video .video-wrapper ul {
    width: 50%;
    flex-direction: column;
    padding: 0;
    max-height: 343px;
    overflow: auto;
}

.movie-details_video .video-wrapper ul li {
    min-width: 100%;
    padding: 20px;
    margin: 0;
    border-radius: 0;
    padding-bottom: 21px;
}

.movie-details_video .video-wrapper ul li:hover {
    background-color: #eee;
    color: #444;
}

.movie-details_video .video-wrapper ul li.active {
    background-color: #4085B1;
    color: #fff;
}

.movie-details_genres ul li, .movie-details_companies ul li {
    max-width: 15%;
    text-decoration: underline;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.movie-details_companies ul li:hover {
    background: none;
    color: #222;
    cursor: default;
}

.movie-details_cast ul li {
    max-width: 10%;
    min-width: 10%;
    margin: 0;
    cursor: default;
    text-align: center;
    line-height: 20px;
}

.movie-details_cast ul li:hover {
    background: none;
    color: #222;
